.container{    

    #banner{
        background: var(--purple);
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 1.5rem;

        padding: 3rem;

        img{
            height: 5rem;
        }

        span{
            font-size: 4rem;
            font-weight: 600;
        }

        p{
            font-size:2.5rem;
            max-width: 500px;
        }
        

    }

    #claim-area{
        max-width: 1080px;
        margin: 0 auto;
        margin-top: 1rem;

        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        background: var(--ultra-dark-purple);

        .grid-2-col{
            gap: 1.5rem;
            display: grid;
            grid-template-columns: 1fr 2fr;
            width: 100%;
            height: 273px;

            #coin-value{
                background: var(--dark-purple);

                display: flex;
                justify-content: center;
                align-items: center;
                flex-direction: column;

                position: relative;

                div{
                    display: flex;
                    gap: 10px;
                    align-items: center;
                    justify-content: center;

                    span{
                        font-size:2rem;
                        font-weight: lighter;
                    }
                }

                button{
                    position: absolute;
                    left: 10px;
                    top: 10px;
                }
                
                img{
                    height: 20px;
                }

                p{
                    position: absolute;
                    bottom: 5px;
                    font-weight: lighter;
                }
            }

            #holdings{
                background: var(--dark-purple);
            }

        }
    }
}